/****************STARS*****************/
#star1,
#star15,
#star3,
#star21,
#star22{
    transform-box: fill-box;
    animation: 3s shineStar linear alternate-reverse infinite;
}
#star20,
#star7,
#star16,
#star9,
#star10,
#star11{
    transform-box: fill-box;
    animation: 4s shineStar linear alternate-reverse infinite;

}
#star12,
#star3,
#star14,
#star2,
#star8,
#star17{
    transform-box: fill-box;
    animation: 5s shineStar linear alternate-reverse infinite;
}
#star18,
#star19,
#star6,
#star13,
#star4{
    transform-box: fill-box;
    animation: 10s shineStar linear alternate-reverse infinite;

}

@-webkit-keyframes shineStar {
    from{
        opacity: 0;
        -webkit-transform: scale(.3);
                transform: scale(.3);

    }
    to{
        opacity: 1;
        -webkit-transform: scale(2);
                transform: scale(2)
    }
}

@keyframes shineStar {
    from{
        opacity: 0;
        -webkit-transform: scale(.3);
                transform: scale(.3);

    }
    to{
        opacity: 1;
        -webkit-transform: scale(2);
                transform: scale(2)
    }
}


/****************PLANES*****************/
/*PLANE1*/
#airplane1,
#airplane2,
#airplane3{
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    transform-box: fill-box;
}

#airplane1{
    -webkit-transform: translateX(-100%) rotateX(10deg);
            transform: translateX(-100%) rotateX(10deg);
    -webkit-animation: 20s plane1Fly linear infinite;
            animation: 20s plane1Fly linear infinite;
}

@-webkit-keyframes plane1Fly {
    0%{
        -webkit-transform: translateX(-100%) scale(1) rotateX(10deg);
                transform: translateX(-100%) scale(1) rotateX(10deg);
    }
    40%,100%{
        -webkit-transform: translateX(1300%) scale(3) rotateX(10deg);
                transform: translateX(1300%) scale(3) rotateX(10deg);
    }
}

@keyframes plane1Fly {
    0%{
        -webkit-transform: translateX(-100%) scale(1) rotateX(10deg);
                transform: translateX(-100%) scale(1) rotateX(10deg);
    }
    40%,100%{
        -webkit-transform: translateX(1300%) scale(3) rotateX(10deg);
                transform: translateX(1300%) scale(3) rotateX(10deg);
    }
}
/*PLANE2*/

#airplane2{
    -webkit-animation: 25s plane2Fly 3s linear infinite;
            animation: 25s plane2Fly 3s linear infinite;
}


@-webkit-keyframes plane2Fly {
    0%{
        -webkit-transform: translateX(0%) scale(1) rotateZ(-5deg);
                transform: translateX(0%) scale(1) rotateZ(-5deg);
    }
    50%,100%{
        -webkit-transform: translateX(-1800%) translateY(-400%) scale(1.5) rotateZ(-5deg);
                transform: translateX(-1800%) translateY(-400%) scale(1.5) rotateZ(-5deg);
    }
}


@keyframes plane2Fly {
    0%{
        -webkit-transform: translateX(0%) scale(1) rotateZ(-5deg);
                transform: translateX(0%) scale(1) rotateZ(-5deg);
    }
    50%,100%{
        -webkit-transform: translateX(-1800%) translateY(-400%) scale(1.5) rotateZ(-5deg);
                transform: translateX(-1800%) translateY(-400%) scale(1.5) rotateZ(-5deg);
    }
}

/*PLANE3*/
#airplane3{
    -webkit-animation:35s plane3Fly linear infinite;
            animation:35s plane3Fly linear infinite;
    -webkit-transform: translateX(-100%) 2.3s translateY(-100%);
            transform: translateX(-100%) 2.3s translateY(-100%);
}


@-webkit-keyframes plane3Fly {
    0%{
        -webkit-transform: translateX(-100%) translateY(-100%);
                transform: translateX(-100%) translateY(-100%);
    }
    12%{
        -webkit-transform: translateX(+430%) translateY(200%) rotateZ(-20deg);
                transform: translateX(+430%) translateY(200%) rotateZ(-20deg);
    }
    14%{
        -webkit-transform: translateX(+500%) translateY(200%) rotateZ(-35deg);
                transform: translateX(+500%) translateY(200%) rotateZ(-35deg);
    }
    50%,100%{
        -webkit-transform: translateX(+2800%) translateY(250%)  rotateZ(-45deg);
                transform: translateX(+2800%) translateY(250%)  rotateZ(-45deg);
    }
}


@keyframes plane3Fly {
    0%{
        -webkit-transform: translateX(-100%) translateY(-100%);
                transform: translateX(-100%) translateY(-100%);
    }
    12%{
        -webkit-transform: translateX(+430%) translateY(200%) rotateZ(-20deg);
                transform: translateX(+430%) translateY(200%) rotateZ(-20deg);
    }
    14%{
        -webkit-transform: translateX(+500%) translateY(200%) rotateZ(-35deg);
                transform: translateX(+500%) translateY(200%) rotateZ(-35deg);
    }
    50%,100%{
        -webkit-transform: translateX(+2800%) translateY(250%)  rotateZ(-45deg);
                transform: translateX(+2800%) translateY(250%)  rotateZ(-45deg);
    }
}

/****************CLOUDS*****************/
#cloud1,
#cloud2{
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    transform-box: fill-box;
    -webkit-transform: translateX(-15%);
            transform: translateX(-15%);
    opacity: .75;
}

#cloud1{
    animation: 10s cloudsHang ease-in-out alternate-reverse infinite;
}

#cloud2{
    animation: 8s cloudsHang ease-in-out alternate-reverse infinite;
}

@-webkit-keyframes cloudsHang{
    from{
        -webkit-transform: translateX(-15%);
                transform: translateX(-15%);
    }
    to{
        -webkit-transform: translateX(15%);
                transform: translateX(15%);
    }
}

@keyframes cloudsHang{
    from{
        -webkit-transform: translateX(-15%);
                transform: translateX(-15%);
    }
    to{
        -webkit-transform: translateX(15%);
                transform: translateX(15%);
    }
}

/****************TREES*****************/
#tree1,
#tree2,
#tree3,
#tree4{
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    transform-box: fill-box;
    -webkit-transform: rotateZ(-1deg);
            transform: rotateZ(-1deg);
}

#tree1{
    animation: 1.5s treeHang ease-in-out alternate-reverse infinite;
}
#tree2{
    animation: 2s treeHang linear alternate-reverse infinite;
}
#tree3{
    animation: 1.7s treeHang linear 300ms alternate-reverse infinite;
}
#tree4{
    animation: 1.8s treeHang linear alternate-reverse infinite;
}

@-webkit-keyframes treeHang {
    from{
        -webkit-transform: rotateZ(-1deg);
                transform: rotateZ(-1deg);
    }
    to{
        -webkit-transform: rotateZ(1deg);
                transform: rotateZ(1deg);
    }
}

@keyframes treeHang {
    from{
        -webkit-transform: rotateZ(-1deg);
                transform: rotateZ(-1deg);
    }
    to{
        -webkit-transform: rotateZ(1deg);
                transform: rotateZ(1deg);
    }
}

.bigButton{
    margin-left: 50%;
    margin-top: 10vh;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);

    font-family: 'Roboto Mono', monospace;
    font-weight: 900;

    background: var(--color1);
    color: var(--color3);
    font-size: var(--fontsize1);

    border-radius: .5vw;
    border: .2vw solid transparent;

    padding: 1vw;

    cursor: pointer;

    box-shadow: 0 0 20px 2px var(--color2);

    transition: 500ms;
    -webkit-transition: 500ms;
    -moz-transition: 500ms;
    -o-transition: 500ms;

}


.bigButton:active,
.bigButton:hover{
    outline: none;
    color: var(--color1);
    border: .2vw solid var(--color1);
    background: transparent;
}


.bigButton:focus{
    outline: none;
    border: .2vw solid white;
    box-shadow: 0 0 30px 2px white;
}
